.owl-carousel .owl-item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    margin-top: 15px;


}

.smaller {
	padding-right: 0px;
}

.hero-holder {
	padding-left: 0;
	padding-right: 0px;
}

.owl-item {
	.item {
		a {

		max-height: 425px;

		.hero {
    height: 425px;
    max-width: 110%;
		}
		}
	}
}

div {
	    border: 0;
    font-family: inherit;
    font-size: 100%;
    font-style: inherit;
    font-weight: inherit;
    margin: 0;
    outline: 0;
    padding: 0;
    vertical-align: baseline;
}



.item a {
    overflow: hidden;
    position: relative;
    display: block;
    text-align: center;
    background-color: #212121;
}

.item a:before {
    content: '';
    position: absolute;
    left: 10px;
    top: 10px;
    right: 10px;
    bottom: 10px;
    border: 1px solid rgba(255,255,255,0.25);
}


.item a img {
    display: block;
    width: calc(100% + 30px);
    max-width: none;
    height: 100%;
    opacity: 0.6;
    transform: translate3d(-30px, 0px, 0px);
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.owl-carousel .owl-item img {
    transform-style: preserve-3d;
}

.item .categories_item_descr {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
}


.item .categories_item_descr span {
    display: block;
    text-transform: uppercase;
    font-weight: 400;
    line-height: 28px;
    font-size: 18px;
    color: #fff;
    transition: color 0.2s ease-in-out;
    -webkit-transition: color 0.2s ease-in-out;
}

.heading_font {
    font-family: 'Oswald', sans-serif;
}

.item .categories_item_descr p {
    margin: 0 0 -20px;
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 20px;
    opacity: 0;
    transform: translate3d(-30px, 0px, 0px);
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.categories_carousel .overlay_border {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.categories_carousel .overlay_border:before, .categories_carousel .overlay_border:before, .categories_carousel .overlay_border:after, .categories_carousel .overlay_border:after {
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
}


.categories_carousel .overlay_border:before {
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transform: scale(0, 1);
    -ms-transform: scale(0, 1);
    transform: scale(0, 1);
}

.categories_carousel .overlay_border:before, .categories_carousel .overlay_border:after {
    content: '';
    display: block;
    position: absolute;
    z-index: 10;
    top: 20px;
    left: 20px;
    bottom: 20px;
    right: 20px;
    -webkit-transition: -webkit-transform 0.35s ease 0s;
    transition: transform 0.35s ease 0s;
}

a {
    color: #333;
    -webkit-transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
    transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}


a:hover, a:focus {
    color: $orange;
}

.item .categories_item_descr p {
    margin: 0 0 -20px;
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 20px;
    opacity: 0;
    transform: translate3d(-30px, 0px, 0px);
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.item a img {
    display: block;
    width: calc(100% + 30px);
    max-width: none;
    height: 100%;
    opacity: 0.6;
    transform: translate3d(-30px, 0px, 0px);
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.owl-carousel .owl-item img {
    transform-style: preserve-3d;
}

.item a:hover {
	background-color: #000;

	img {
		  width: calc(100% + 30px);
		      opacity: 0.2;
    transform: translate3d(0px, 0px, 0px);
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
	}
	p {
		    opacity: 1;
		   transform: translate3d(0px, 0px, 0px);
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
	}
}


