#sub-slider-holder {
	margin: 1.5em 0px;
	background: $teal;
	color: #FFF;
	.sub-slider {

		height: 75px;
		h4 {
			font-size: 0.9;
			position: relative;
			margin: 0;
			padding: 0;
			top: 50%;
			left: 50%;
			/* bring your own prefixes */
			transform: translate(-50%, -50%);
			text-align: center;
			font-family: 'Open Sans', sans-serif;
			.smaller-h4 {
				font-size: 0.7em;
			}
		}
	}
}


#page-header-holder {
	background: $teal;
	color: #FFF;
	height: 75px;
	.page-header {
		display: inline;
		h4 {
			float: left;
			font-size: 0.9;
			font-family: 'Open Sans', sans-serif;
			.smaller-h4 {
				font-size: 0.7em;
			}
			#search-box {
				float: right;
			}
		}
	}
}


.page-banner-holder {
	margin-top: 1.5em;
	margin-bottom: 1.5em;
	.page-banner {
		height: 60px;
		h4 {
  			float: left;
			margin: 0;
			padding: 0;
  			padding: 16px 0px;
			color: #FFF;
		}
		#search-box {
			width: 35%;
			float: right;
			padding-top: 10px;
			.widget_search .search_form input[type=text] {

			}
		}
	}
}


.gform_wrapper .top_label li.gfield.gf_right_half input.medium, .gform_wrapper .top_label li.gfield.gf_left_half input.large, .gform_wrapper .top_label li.gfield.gf_left_half input.medium, .gform_wrapper .top_label li.gfield.gf_left_half select.large, .gform_wrapper .top_label li.gfield.gf_left_half select.medium, .gform_wrapper .top_label li.gfield.gf_right_half input.large, .gform_wrapper .top_label li.gfield.gf_right_half input.medium, .gform_wrapper .top_label li.gfield.gf_right_half select.large, .gform_wrapper .top_label li.gfield.gf_right_half select.medium {
	width: 102%;
}

.gform_wrapper {
	max-width: 100%;
	margin-bottom: 5em;
}

#gform_submit_button_1 {
	background: $orange;
	width: 15vh;
}

.gform_wrapper textarea.medium {
	width: 100%;
}

.gfield_required {
	display: none;
}



@media (min-width: 1px) and (max-width: 768px) {



.page-banner-holder {
	margin-top: 1.5em;
	margin-bottom: 1.5em;
	.page-banner {
		height: 60px;
		h4 {
  			float: left;
			margin: 0;
			padding: 0;
  			padding: 16px 0px;
			color: #FFF;
		}
		#search-box {
			width: 100%;
  			padding-bottom: 1em;
			float: right;
			padding-top: 10px;
}

		}
	}


.post-title {
	margin: 0;

}
