/*==========  Typography  ==========*/

html {font-size: 1em;}

body {
  background-color: white;
  font-family: Open Sans;
  font-weight: 400;
  line-height: 1.8em;
  color: $grey;
}

p {margin-bottom: 1.3em;}

h1, h2, h3, h4 {
  margin: 1.414em 0 0.5em;
  font-weight: inherit;
  line-height: 1.2;
}

h1 {
  margin-top: 0;
  font-size: $fontH1;
}

h2 {font-size: $fontH2;}

h3 {font-size: $fontH3;}

h4 {font-size: $fontH4;}

small, .font_small {font-size: $fontSmall;}

ul {
  margin-bottom: 5px;
}


h2 + h2 {
    font-size: 1.8em;
    font-weight: 600;
    margin-top: -0.3em !important;
    color: #696A7C !important;
}