.articles-title {
	text-transform: uppercase;
	font-family: Oswald,sans-serif;
    font-weight: 400;
    letter-spacing: 2px;
}

.meta-posted-name {
	text-transform: uppercase;
	font-family: Oswald,sans-serif;
    letter-spacing: 2px;
}

.meta-posted-by {
	font-size: 0.8em;
	padding-right: 0.2em;
}


.feat-image {
	img {
		max-width: 100%;
		width: 100%;
	}
	max-height: 650px;
	overflow: hidden;
    margin-bottom: 1.3em;
}

#article-content {
	margin-bottom: 4em;
}

.comment-form-comment {
	label {
		display: none;
	}
}

#commentform {
#comment {
	width: 100%;
}

	.form-submit {
		#submit {
			background: #C94429;
    padding: 5px 10px;
		}
	}
}

.comment {
	    border: 1px solid #CCC;
	    border-left: 10px solid $orange;
    padding: 15px;
    margin: 30px 0px;
}


/*=================================================
=            Successs Stories / Quotes            =
=================================================*/

.header-quote {

position: relative;
    margin-bottom: 1em;
    background: #000;

.quote-content {
	position: absolute; 
	top: 2em;
	color: #FFF;
	padding: 1em;
	width: 100%;
	height: 300px;
	display: table;
    margin-top: -2em;
	p {
    bottom: -1%;
    left: 1%;
    position: absolute;
	}
	h2 {
		color: #FFF;
		font-weight: 800;
		font-size: 1.6em;
    	margin-top: 0.5em;
		display: table-cell; 
		vertical-align: middle;
		text-align:center	
}
}


	.john-demartini {
width: 100%;
height: 300px;
background: url("/wp-content/themes/DrEspen/dist/images/john-demartini.jpg");
background-size: cover;
opacity: 0.4;

	}

	.courier-mail {
width: 100%;
height: 300px;
background: url("/wp-content/themes/DrEspen/dist/images/courier-mail.jpg") center center;
background-size: cover;
opacity: 0.4;
	}
}

.quote-name-bottom {
    bottom: -1%;
    left: 4%;
    position: absolute;
}

.quote-name-bottom-left {
    bottom: -1%;
    left: 6%;
    position: absolute;
}



@media (min-width: 1px) and (max-width: 333px) {
	.header-quote {
	.courier-mail {
		height: 430px;
	}

	.john-demartini {
		height: 430px;
	}
	.quote-content {
		h2 {
			font-size: 1.2em;
		}
	}
	}

	.header-quote .quote-content p {
		bottom: -29%;
	}
}

@media (min-width: 334px) and (max-width: 550px) {
	.header-quote {
	.quote-content {
		h2 {
			font-size: 1.2em;
		}
	}
	}

}








/*=====  End of Successs Stories / Quotes  ======*/


/* ============================================================
  CUSTOM PAGINATION
============================================================ */
.custom-pagination span,
.custom-pagination a {
  display: inline-block;
    padding: 10px 16px;
}
.custom-pagination a {
  background-color: #ebebeb;
  color: $orange;
}
.custom-pagination a:hover {
  background-color: $orange;
  color: #fff;
}
.custom-pagination span.page-num {
  margin-right: 10px;
  padding: 0;
}
.custom-pagination span.dots {
  padding: 0;
  color: $teal;
}
.custom-pagination span.current {
  background-color: $orange;
  color: #fff;
}


/* Commenting */

.comment-form-url {
	display: none;
}

#programs, #events {
	.post_format_content {
		    max-height: 400px;
	}
}

.synved-social-button {
		height: 60px !important;
		width: 60px !important; 
	img {
		height: 50px !important;
		width: 50px !important; 
	}
}

@media (min-width: 1px) and (max-width: 600px) {

.articles-title {
	font-size: 1.8em;
}

	}

@media (min-width: 1px) and (max-width: 1200px) {
	.w-100-responsive {
		width: 100%;
	}
}

