
.navbar-header {
	height: 70px;
}

.navbar-brand {
	padding-left: 0;
padding: 10px 15px;
	img {
height: 43px;
    padding-top: 5px;
    max-width: 100%;
	}
}

.sub-nav {
	width: 100%;
	background: $orange;
	height: 50px;
	color: #FFF;
	a {
		color: #FFF;
	}
	.navbar-collapse {
		padding: 0;
	}

	ul {

		display: inline;
		li {
			list-style-type: none;
		}
	}
}


.shop-menu {
	li {
		a {
			color: #FFF;
			    padding: 15px 25px;
			    font-weight: bold;
			font-family: 'Open Sans', sans-serif;
			&:hover {
				color: $teal;
			}
		}
	}
}


.sub-nav-shop {
	width: 100%;
	background: $teal;
	height: 50px;
	color: #FFF;
	a {
		color: #FFF;
		&:hover, &:focus {
			color: $teal;
		}
	}
	.navbar-collapse {
		padding: 0;
	}


	ul {

		display: inline;
		li {
			list-style-type: none;
		}
	}
}



	.navbar-collapse.in {
		overflow-y: visible !important;
	}

#menu-primary_navigation-1, #menu-health-sub-menu {
	li {
		a {
			color: #FFF;
			    padding: 15px 25px;
			    font-weight: bold;
			font-family: 'Open Sans', sans-serif;
			&:hover {
				color: $orange;
			}
		}
	}
}

.navbar-center {
	text-align: center;
}


.navbar {
	margin-bottom: 0;
}

#menu-primary_navigation {
	//height: 72px;
		li {
			height: 100%;
			a {
				color: $orange;
				font-size: 1.2em;
				font-weight: bold;
			font-family: 'Open Sans', sans-serif;
			    padding: 10px 25px;
			    padding-top: 25px;
			    height: 72px;
			}
		}
		.sub-menu {
			display: none;
		}
}

@media (min-width: 0px) and (max-width: 1140px) {
	#prim-nav {
		display: none;
	}
	.sub-nav, .sub-nav-shop {
		display: none;
	}
}
