@import "_variables.scss";

::-moz-selection { background-color: darken($yellow, 5%) !important; }
::selection { background-color: darken($yellow, 5%) !important; }


/*==========  General Classes  ==========*/

.center-text {
	text-align: center;
}

.white_bg {
	background: $white;
}

.white-grey_bg {
	background: $white-grey;
}

.grey_bg {
	background: $grey;
}

.light-grey_bg {
	background: $light-grey;
}

.m-t-l {
	margin-top: 18vh;
}

.orange_bg {
	background: $orange;
}

.teal_bg {
	background: $teal;
}

.purple_bg {
	background: $purple
}

.m-t-s {
	margin-top: 5em;
}

.m-b-s {
	margin-bottom: 1em;
}

.m-b-m {
	margin-bottom: 2.5em;
}



/*----------  Remove Padding, Margins and Borders  ----------*/

.n-p {
	padding: 0;
}

.n-b {
	border: 0;
}

.n-m {
	margin: 0;
}


/*----------  Text Alignment  ----------*/

.t-c {
	text-align: center;
}

.t-l {
	text-align: left;
}

.t-r {
	text-align: right;
}

.t-j {
	text-align: justify;
}


/*----------  General Text  ----------*/

.t-uc {
	text-transform: uppercase;
}

.t-lc {
	text-transform: lowercase;
}


/*----------  Text Changes  ----------*/

.white-text {
	color: #FFF;
}

.yellow-text {
	color: $yellow;
}


.orange-text {
	color: $orange;
}

.p-l-0 {
	padding-left: 0;
}

.p-r-0 {
	padding-left: 0;
}


/* IMPORTANT STYLING FOR WORDPRESS  */ 



img.alignright { float: right; margin: 0 0 1em 1em; }
img.alignleft { float: left; margin: 0 1em 1em 0; }
img.aligncenter { display: block; margin-left: auto; margin-right: auto; }
.alignright { float: right; }
.alignleft { float: left; }
.aligncenter { display: block; margin-left: auto; margin-right: auto; }